/* Main Container */
.food-donation-container {
  max-width: 950px;
  margin: 40px auto;
  padding: 40px 30px;
  background: linear-gradient(135deg, #0f2027, #203a43, #2c5364);
  border-radius: 20px;
  font-family: 'Poppins', sans-serif;
  color: #f0f4f8;
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.25);
}

/* Header */
.food-donation-container h2 {
  text-align: center;
  color: #ffffff;
  font-size: 2.6rem;
  font-weight: 700;
  margin-bottom: 35px;
  text-shadow: 1px 2px 8px rgba(0, 0, 0, 0.4);
}

/* Donation Type Buttons */
.donation-type-buttons {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-bottom: 35px;
}

.donation-type-buttons button {
  padding: 18px;
  font-size: 1.1rem;
  border-radius: 12px;
  border: none;
  cursor: pointer;
  font-weight: 600;
  color: white;
  letter-spacing: 0.5px;
  transition: all 0.3s ease;
}

.donation-type-buttons button:nth-child(1) {
  background: linear-gradient(135deg, #00b09b, #96c93d);
}
.donation-type-buttons button:nth-child(2) {
  background: linear-gradient(135deg, #007adf, #00ecbc);
}
.donation-type-buttons button:nth-child(3) {
  background: linear-gradient(135deg, #ff416c, #ff4b2b);
}

.donation-type-buttons button:hover {
  transform: translateY(-4px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.25);
}

/* Form Container */
.food-donation-form {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(15px);
  padding: 30px;
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  gap: 22px;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

/* Labels & Inputs */
.food-donation-form label {
  display: flex;
  flex-direction: column;
  font-size: 1rem;
  font-weight: 500;
  color: #e0e8ef;
}

.food-donation-form input {
  padding: 12px 14px;
  border-radius: 10px;
  border: 1px solid #ccc;
  margin-top: 8px;
  font-size: 1rem;
  outline: none;
  transition: border 0.3s ease, box-shadow 0.3s ease;
}

.food-donation-form input:focus {
  border-color: #00c6ff;
  box-shadow: 0 0 8px rgba(0, 198, 255, 0.5);
}

/* Food Item Block */
.food-item {
  border: 1px dashed rgba(255, 255, 255, 0.3);
  padding: 18px;
  border-radius: 12px;
  position: relative;
  background: rgba(255, 255, 255, 0.05);
  transition: all 0.3s ease;
}

.food-item:hover {
  background: rgba(255, 255, 255, 0.1);
  transform: scale(1.02);
}

.food-item h4 {
  margin-bottom: 10px;
  color: #ffffff;
  font-weight: 600;
}

.food-item button {
  margin-top: 10px;
  background: linear-gradient(135deg, #ff512f, #dd2476);
  color: white;
  border: none;
  padding: 10px 14px;
  border-radius: 10px;
  font-weight: 600;
  cursor: pointer;
  transition: 0.3s;
}

.food-item button:hover {
  background: linear-gradient(135deg, #e43a15, #e65245);
}

/* Submit Button */
.food-donation-form button[type="submit"] {
  background: linear-gradient(135deg, #11998e, #38ef7d);
  padding: 16px;
  font-size: 1.2rem;
  color: white;
  font-weight: bold;
  border-radius: 12px;
  border: none;
  cursor: pointer;
  transition: 0.3s ease;
}

.food-donation-form button[type="submit"]:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
}

/* Tracking Section */
.tracking-section {
  margin-top: 45px;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  padding: 25px;
  border-radius: 15px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: #e8f0f8;
}

/* Responsive */
@media (max-width: 600px) {
  .food-donation-container {
    padding: 25px 20px;
  }
}
